<script>
  import { Line } from 'vue-chartjs'
  import axios from 'axios';

  export default {
    extends: Line,
    data () {
      return {
         caselist:[],
         datelist:[],
        chartData: {
          labels: '',
          datasets: [
            {
              label: 'Line Chart',
              data: '',
              fill: false,
              borderColor: '#2554FF',
              backgroundColor: '#2554FF',
              borderWidth: 1
            }
          ],

      }
      }
    },
    async created () {

      axios.get('http://localhost:8888/api/girls')
          .then(res => {
             const data = res.data
             var i
             console.log("chunsa")
             console.log(data.length)
             for(i=0;i<data.length;i++){
                this.caselist.push(data[i].NUMOFAPPROACHED)
                this.datelist.push(data[i].YEAR)
             }         
                
              
              
          this.chartData= {
             labels: this.datelist,
             datasets: [
               {
              label: 'PUA',
              data: this.caselist,
              fill: false,
              borderColor: '#2554FF',
              backgroundColor: '#2554FF',
              borderWidth: 1
               }
             ]
          }
            
          this.renderChart(this.chartData, this.options)
         })
        .catch(error => console.log(error))
    
    },
    mounted () {
      this.renderChart(this.chartData, this.options)
    }
  }
</script>